<script>
import axios from 'axios'
import global from '../global'
import FileManage from '@/components/FileManage'
import PublicNoteManage from '@/components/PublicNoteManage'
import EditorSetting from '@/components/EditorSetting'

export default {
    name: "admin",
    components: {
      FileManage,
      PublicNoteManage,
      EditorSetting
    },
    data() {
      return {
        gitActive: 0,
        genSshKey: false,
        sshkey: "",
        remoteRepoUrl: null,
        oldPassword : null,
        newPassword : null,
        config : {
          headers: {
            token : this.$store.getters.getToken,
            username : this.$store.getters.getUsername
          }
        }
      };
    },
    methods: {
      handleStartPush(){
        let url = global.HOST_URL + "/user/push";
            axios.post(url, null, this.config).then(res => {
              res = res.data;
              if(res.code === 0){
              this.$notify({
              type: 'success',
              message: 'ÂºÄÂßãÂ§á‰ªΩËá≥ËøúÁ®ã‰ªìÂ∫ì',
              duration: 1000
            });
              }
            }
          )
      },
      handleStopPush(){
          let url = global.HOST_URL + "/user/push";
            axios.delete(url, this.config).then(res => {
              res = res.data;
              if(res.code === 0){
              this.$notify({
              type: 'success',
              message: 'ÂÅúÊ≠¢Â§á‰ªΩ',
              duration: 1000
            });
              }
            }
          )
      },
      handleSetRemoteRepo(){
        let param = {
          remoteRepoUrl : this.remoteRepoUrl
        }
        let url = global.HOST_URL + "/user/remote";
        axios.put(url, param, this.config).then(res => {
          res = res.data;
          if(res.code === 0){
            this.$notify({
                    type: 'success',
                    message: 'ËÆæÁΩÆËøúÁ®ã‰ªìÂ∫ìÊàêÂäü'
                  });
          }
        })
      },
      handleGenSshKey(){
        this.genSshKey = false;
        let url = global.HOST_URL + "/user/sshkey";
        axios.post(url, null, this.config).then(res => {
            res = res.data;
            if(res.code !== 0){
              return ;
            }
            this.sshkey = res.data;
            this.genSshKey = true;
        })
      },
      notifyLoadEditorConfig(){
        console.log("fdsfsdfsdfsf")
this.$refs.editorSetting.loadEditorConfig();
      },
      tabClick(target){
        if(target.$attrs.mname === 'editorConfigTab') {
          this.notifyLoadEditorConfig();
        }
      },
      handleChangePassword(){
          let url = global.HOST_URL + "/user/changePass";
          let request = {
            password : this.oldPassword,
            newPassword : this.newPassword
          };
          axios.post(url, request, this.config).then(
            res => {
              res = res.data;
              if(res.code === 0){
                  this.$notify({
                    type: 'success',
                    message: '‰øÆÊîπÂØÜÁ†ÅÊàêÂäü'
                  });
                  this.newPassword = null,
                  this.oldPassword = null
              }else{
                this.$notify({
                    type: 'warning',
                    message: res.msg
                  });
              }
            }
          )
      }
    },
    mounted () {
      document.title = this.$store.getters.getWebsiteTitle + " - ËÆæÁΩÆ"
      if(!this.config.headers.token){
      this.$notify({
                    type: 'warning',
                    message: 'ÈúÄË¶ÅÁôªÂΩï'
                  });
      setTimeout(()=>{this.$router.push('/login');},700);
      return ;
    }
    let url = global.HOST_URL + "/user/validate";
    // console.log(this.config)
    axios.post(url, null, this.config).then(
      res => {
        res = res.data;
        if(res.code === 0){
          return ;
        }
        this.$store.commit('setLocalInfo', {});
        this.$notify({
                  type: 'warning',
                  message: 'ÈúÄË¶ÅÁôªÂΩï',
                  duration: 1500
            });
        setTimeout(()=>{this.$router.push('/login');},700);
        
      }
    )
    // TODO user info
    let getRemoteRepoUrl = global.HOST_URL + "/user/remote";
    axios.get(getRemoteRepoUrl, this.config).then(
      res => {
        res = res.data;
        if(res.code === 0){
          this.remoteRepoUrl = res.data;
        }
      }
    )
    }
  };
</script>
